
// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
// ** Invoice List Sidebar

// import AddQuiz from '../add'

// ** Store & Actions
import { getfootballTotal, clearData, getFootballContests, getFootballContestadd, viewfootballcontestprize, deletefootballcontestprizes } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter, Edit, Trash2  } from 'react-feather'
import DataTable from 'react-data-table-component'
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, Button, Spinner } from 'reactstrap'
import { getAllSeries, getAllFixture, collectseriesid, collectfixtureid, getval } from '../../../Report/football/store/actions'
// ** Styles
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import BreadCrumbs from '@components/breadcrumbs'
import Select from 'react-select'
import { useHistory } from 'react-router-dom'
import { selectThemeColors } from '@utils'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import AppCollapse from '@components/app-collapse'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

// ** Table Header
const CustomHeader = ({ setListData, football_data, football_add_data, setCurrentPage }) => {
  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit } = useForm({ defaultValues })
  const [data, setData] = useState(null)
  const store = useSelector((state) => state.reports)
  const store1 = useSelector((state) => state.contestsprizes)
  const dispatch = useDispatch()

  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [serilist, setSerilist] = useState("")
  const [fixlist, setFixlist] = useState("")
  const [refresh, setRefresh] = useState(false)
  const [click, setClick] = useState(false)
  const [val, setVal] = useState(false)
  const [error, setError] = useState(false)
  const [page, setPage] = useState(1)
  const [value] = useState(null) 

  const fixture = store.football_fixture_name.map(function (fix) {
    return { value: fix.id, label: fix.fixture_name }
  })

  const filterid = store.football_series_name.filter(s => s.series_name === serilist)
  const filterid2 = store.football_fixture_name.filter(f => f.fixture_name === fixlist)
  const filterall = store.football_fixture_name.map(f => f.fixture_name)
  console.log(data, sid, error, filterall)

  useEffect(() => {
    dispatch(getfootballTotal(page))
  }, [page])

  useEffect(() => {
    console.log('Component Mounted')
    return () => {
      console.log('component Unmounted')
      dispatch(clearData())
    }
  }, [])

  useEffect(() => {
    dispatch(getAllFixture(serilist))
    console.log(serilist)
    console.log(store.football_fixture_name)
    console.log(store)
    console.log(sid)
  }, [serilist])

  const idea = `{"series_id":${serilist}, "fixture_id":"${click ? fixlist : ""}", "page":1}`
  const submit = () => {
    setCurrentPage(1)
    setData(serilist)
    setVal(true)
    console.log(serilist)
    console.log(data)
    //  e.preventDefault()
    console.log(filterall.map(f => f === fixlist))
    dispatch(collectseriesid(serilist))
    dispatch(collectfixtureid(fixlist))
    console.log(idea)
    console.log(fixlist)
    if (serilist === "") {
      setError(true)
    } else {
      dispatch(getFootballContestadd(idea))

    }
    console.log(`"series":${filterid[0]?.id}, "fixture":${fixlist}, "page":${1} `)
  }

  useEffect(() => {
    if (football_add_data.length !== 0) {
      setListData(football_add_data)
    } else {
      if (football_add_data.length === 0 && val) {                 
        setListData(football_add_data)
      } else {      
        setListData(football_data)        
      }  
    }
  }, [submit])

  const serieslist = (e) => {
    console.log(e)
    console.log(error)
    setVal(false)
    setError(false)
    setSerilist(e?.value)
    setRefresh(true)
    dispatch(getval(false))
    setSid(filterid[0]?.id)
    setClick(false)
  }

  const fixturelist = (e) => {
    const datas = e.map(e => e.value)
    setFixlist(datas)
    setRefresh(false)
    dispatch(getval(true))
    setFid(filterid2[0]?.id)
    setClick(true)
  }

  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)    
      dispatch(getFootballContestadd(idea))
      dispatch(collectseriesid(serilist))
      dispatch(collectfixtureid(fixlist))
    }
  }

   // ************************************************************************************* //

   const options = []
   for (let i = 0; i < store1.football_total?.data?.total; i++) {
     if (page === store1.football_total?.data?.current_page) {
       options.push({
         value: `${store1.football_total?.data?.data[i]?.id}`,
         label: `${store1.football_total?.data?.data[i]?.series_name}`
       })
     }
     
   }
   const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
   const optionsPerPage = 10
   
   const loadOptions = async (search, prevOptions, page) => {
     await sleep(1000)
     if (page === store1.football_total?.data?.current_page) {
       const nextData = store1.football_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
       const scrollLength = [...prevOptions, ...nextData]
       let filteredOptions
       if (!search) {
         filteredOptions = scrollLength.length > 10 ? scrollLength : options
       } else {
         const searchLower = search.toLowerCase()
         filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
         )
       }
   
       const hasMore = Math.ceil(store1.football_total?.data?.total / optionsPerPage) > page
       const slicedOptions = filteredOptions.slice(
         (page - 1) * optionsPerPage,
         page * optionsPerPage
       )
   
       return {
         options: slicedOptions,
         hasMore
       }
     }
   }
   const defaultAdditional = {
     page: 1
   }
   
   const loadPageOptions = async (q, prevOptions, { page }) => {
     setPage(page)
     const { options, hasMore } = await loadOptions(q, prevOptions, page)
     return {
       options:[...options],
       hasMore,
   
       additional: {
         page: page + 1
       }
     }
   }
   
    // ************************************************************************************* //


  const data1 = [
    {
      title: <div><Filter size={18}/> Filter...</div>,
      content: (
        <>
        <Row>
        <Col className='mb-1' md='6' sm='12'>
          <Label>series</Label>
              <AsyncPaginate
              menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
             {serilist === "" && val ?  <h5 className='text-danger'>Please enter a value</h5> : ""}
          </Col>
          <Col className='mb-1' md='6' sm='12'>
            <Label>Fixture</Label>
            <Select
              isDisabled={store.fixloader}
              onChange={(e) => fixturelist(e)}
              theme={selectThemeColors}
              value={refresh ? "" : fid}
              className='react-select'
              classNamePrefix='select'
              isMulti
              // defaultValue={fixture[0]}
              options={fixture ? fixture : ""}
              placeholder='Select football fixture'
              isClearable={true}
            />
          </Col>
        </Row>
        <Button.Ripple
          color='primary'
          onClick={handleSubmit(submit)}
        >submit</Button.Ripple>
      </>
      )
    }
  ]

  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
          <AppCollapse data={data1} type='border' accordion className='mb-1' />
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.contestsprizes)
  const sto = useSelector((state) => state.reports)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [paginate, setPaginate] = useState(0)
  const MySwal = withReactContent(Swal)
  const history = useHistory()
  // const [sidebarOpen, setSidebarOpen] = useState(false)
  // ** Get data on mount
  useEffect(() => {
    const idea = `{"series_id":${sto.football_seriesid}, "fixture_id":"${sto.getval ? sto.football_fixtureid : ""}", "page":${currentPage}}`
    console.log(sto.football_fixtureid)

    if (store.football_add_data.length !== 0) {
      dispatch(getFootballContestadd(idea))
    } else {
      dispatch(getFootballContests(currentPage))
    }

  }, [paginate])

  useEffect(() => {
    dispatch(getAllSeries())
  }, [currentPage])


  useEffect(() => {
    if (store.isloading) {
      <Spinner />
      console.log("click")
      console.log(store.isloading)
    } else {
      setListData(store.football_data)
    }
  }, [store.isloading])

  //const columnsList = useCallback(columns(), [])
  // ** User filter options

  useEffect(() => {
    dispatch(clearData())
  }, [])

  const handleConfirmText = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {
      if (result.value) {
        dispatch(deletefootballcontestprizes(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Your file has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const editHandler = (row) => {
    history.push({pathname:"/football/contestprize/edit"})
    dispatch(viewfootballcontestprize(row.id))    
  }

  const columns = [
    {
      name: '#',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) =>  `#${store.football_prize_page.from + pos}` 
    }, 
    {
      name: 'Image',
      minWidth: '100px',
      selector: 'feed_image',
      sortable: true,
      cell: (row) => (
        <img src={row.contest_prize_assets_url} style={{ height: '40px', width: '40px'}} />
      )
    },
    {
      name: 'rank',
      minWidth: '160px',
      selector: 'type',
      sortable: true,
      cell: (row) => row.prize_rank
    },
    
    {
      name: 'challenges_name',
      minWidth: '160px',
      selector:'countdown',
      sortable: true,
      cell: (row) => row.contest_name
    
    },
    {
      name: 'prizeType',
      minWidth: '160px',
      selector:'countdown',
      sortable: true,
      cell: (row) => row.contest_prize_type
    
    },
    //split(/\s+/).reverse().join(" ")
    {
      name: 'prizecaption',
      minWidth: '160px',
      selector:'countdown',
      sortable: true,
      cell: (row) => (row.contest_prize_caption.split(/\s+/).reverse().join(" ").match(/^(.*)(.{6})/).slice(1).join(','))
    
    },
    {
      name: 'prizeValue',
      minWidth: '160px',
      selector:'countdown',
      sortable: true,
      cell: (row) => row.contest_prize_value
    
    },
    {
      name: 'Active',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit Feed" onClick={() => editHandler(row)} />
          {/* </a> */}
          <Trash2 width="17" className="ml-2 cursor-pointer" title="Add Feed" onClick={() => handleConfirmText(row.id)} />
        </div>
      )
    }
  ]

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.football_data.filter((userItem) => {

        if (userItem.player_type) {
          return userItem.player_type.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.football_data)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = store.football_prize_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    // if (store.allContests.length > 0) {
    //   return store.allContests
    // } else
    if (listData && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Football Challenges prize' breadCrumbActive='List' />
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              currentPage={currentPage}
              handleFilter={handleFilter}
              setListData={setListData}
              football_data={store.football_data}
              football_add_data={store.football_add_data}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList